/* Works-section */
.works-section{
    margin-top: 100px;
    padding-top: 120px;
}

.w_title {
    padding-top: 20px;
}

.w_line {
    margin: 15px 0;
}

.w_text {
    margin-bottom: 30px;

    @include mq(435) {
        br {
            display: none;
        }
    }

    @include mq(390) {
        max-width: 245px;
    }
}

.w_grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    grid-gap:30px;

    @include mq(480) {
        grid-template-columns: 1fr;
    }
}

.w_grid-box{
    position: relative;
    overflow: hidden;

    .grid_box-inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 96%;
        width: 97%;
        border-radius: 30px;
        overflow: hidden;
    }

    img {
        position: relative;
        object-fit: cover;
        height: 100%;
        width: 100%;
        border-radius: 30px;
        overflow: hidden;
    }

    .overlay{
        position: absolute;
        z-index: 5;
        transform:translate(-50%, -50%) scale(0);
        width: 300%;
        height: 300%;
        border-radius: 50%;
        background: $primary-color-op;
        color: $light-color;
        padding: 0.3rem;
        transition: .8s visibility, .8s transform ;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        visibility: hidden;
    }

    .grid_box-title {
        position: relative;
        z-index: 10;
        display: inline-block;
        top: 50%;
        left: 50%;
        transform: translate(-50% ,-50%) scale(0);
        opacity: 0;
        color: $light-color;
        transition: .8s opacity, .8s transform;
    }

    @include hover {
        .overlay {
            visibility: visible;
            transform:translate(-50%, -50%) scale(1);
        }

        .grid_box-title {
            transform: translate(-50% ,-50%) scale(1);
            opacity: 1;
        }
    }
}