/* Contact-section */
.contact-section{
    margin: 100px 0 220px;
    padding-top: 120px;
}

.c_flex {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;

    @include mq(950) {
        flex-direction: column;
    }
}

.c_line {
    margin: 15px 0 30px;
}

.c_content {
    max-width: 380px;
    margin-right: 50px;

    &.dark-state {
        .c_mail-link {
            color: $light-color;
        }

        .c_upwork {
            box-shadow: $shadow-dark;
            background: $dark-color;

            svg {
                .outline {
                    fill: $light-color;
                }
    
                .letters {
                    fill: $dark-color;
                }
            }

            @include hover {
                svg {
                    .letters {
                        fill: $primary-color;
                    }
                }
            }
        }
    }

    @include mq (950) {
        margin-right: 0;
    }
}

.c_text {
    margin-bottom: 15px;
}

.c_mail,
.c_phone {
    position: relative;
    left: -5px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 5px;

    svg{
        fill: $primary-color;
        path {
          stroke: none;
        }
    }

    .c_mail-link {
        color: $black-color;
        transition: 0.5s color;
        will-change: color;

        @include hover {
            color: $primary-color;
        }
    }
}

.c_upwork {
    padding: 15px 15px 10px 15px;
    border-radius: 50%;
    margin-top: 30px;
    svg {
        width: 30px;
        height: 30px;
        
        .outline {
            transition: 0.5s fill;
            will-change: fill;
        }

        .letters {
            transition: 0.5s fill;
            will-change: fill;
        }
    }

    @include hover {
        svg {
            .outline {
                fill: $light-color;
            }

            .letters {
                fill: $primary-color;
            }
        }
    }
}

.c_form {
    width: 60%;
    display: grid;
    grid-template-areas: 
    "name email"
    "subject subject"
    "message message";
    grid-gap: 15px;

    .input{
        padding: 15px 25px;
        border-radius: 30px;
        outline: none;
        border: none;
        cursor: none;
        box-shadow: $shadow;
        background: $medium-color;
    }

    &.dark-state {
        .input {
            box-shadow: $shadow-dark;
            background: $dark-color;
            color: $light-color;

            &::placeholder {
                color: $light-color;
                opacity: 0.7;
            }
        }
    }

    @include mq(950) {
        width: 100%;
        margin-top: 50px;
    }

    @include mq(650) {
        grid-template-areas: 
        "name"
        "email"
        "subject"
        "message";
    }
}

.c_btn {
    outline: none;
    border: none;
    width: 130px;
    font-weight: bold;
    background:$medium-color;
    color: $primary-color;
    box-shadow: $shadow;
}

/* Grid area's */
.name-input{
    grid-area: "name";
}

.email-input{
    grid-area: "email";
}

.subject-input{
    grid-area: "subject";
    grid-column-start: span 2;

    @include mq(650) {
        grid-column-start: span 1;
    }
}

.message-input{
    grid-area: "message";
    grid-column-start: span 2;
    height: 100px;

    @include mq(650) {
        grid-column-start: span 1;
    }
}