/* About-section */
.about-section {
    margin-top: 100px;
    padding-top: 120px;
}

.a_title {
    padding-top: 20px;
}

.a_line {
    margin: 15px 0 30px;
}

.a_flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &.dark-state .a_img-container {
        background: $dark-color;
        box-shadow: $shadow-dark;
    }

    &.dark-state .a_content {
        background: $dark-color;
        box-shadow: $shadow-dark;
    }

    @include mq(1050) {
        flex-direction: column;
    }
}

.a_img-container {
    position: relative;
    width: 300px;
    height: 300px;
    background-color: $medium-color;
    border-radius: 50%;
    box-shadow: $shadow;

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 96%;
        height: 96%;
        object-fit: cover;
        border-radius: 50%;
    }

    @include mq(650) {
        width: 230px;
        height: 230px;
    }
}

.a_content {
    width: 60%;
    height: auto;
    background-color: $medium-color;
    border-radius: 30px;
    box-shadow: $shadow;
    padding: 30px;
    margin-left: auto;

    @include mq(1050) {
        margin: 50px 0 0 0;
        width: 100%;
    }

    @include mq(650) {
        padding: 15px;
    }
}

.a_btn {
    padding: 15px 15px 12px 15px;
    border-radius: 50%;
    margin-top: 30px;
}

.linkedin {
    margin-right: 15px;
}

.github {
    svg {
        transition: 0.5s fill;
        will-change: fill;
        fill: $primary-color;
    }

    @include hover {
        svg {
            fill: $light-color; 
        }
    }

    &.dark-state {
        svg {
            fill: $light-color;
        }
    }
}