@font-face {
    font-family: "M PLUS Rounded 1c";
    src: url("../../public/fonts/RoundedMplus1c-Regular.woff2") format("woff2"),
        url("../../public/fonts/RoundedMplus1c-Regular.woff") format("woff");
    font-style: normal;
    font-weight: normal;
    font-display: swap;
}

@font-face {
    font-family: "M PLUS Rounded 1c";
    src: url("../../public/fonts/RoundedMplus1c-Bold.woff2") format("woff2"),
        url("../../public/fonts/RoundedMplus1c-Bold.woff") format("woff");
    font-style: normal;
    font-weight: normal;
    font-display: swap;
}

@font-face {
    font-family: "Varela Round";
    src: url("../../public/fonts/VarelaRound-Regular.woff2") format("woff2"),
        url("../../public/fonts/VarelaRound-Regular.woff") format("woff");
    font-style: normal;
    font-weight: 300;
    font-display: swap;
}

// Utility font classes

// VARELA fonts

.varela-16-normal {
    font-family: "Varela Round";
    font-style: normal;
    font-size: 16px;
    font-weight: 300;
    line-height: 100%;

    @include mq(750) {
        font-size: 15px;
    }

    @include mq(374) {
        font-size: 14px;
    }
}

.varela-18-normal {
    font-family: "Varela Round";
    font-style: normal;
    font-size: 18px;
    line-height: 110%;
    font-weight: 300;

    @include mq(750) {
        font-size: 16px;
    }
}

.varela-20-normal {
    font-family: "Varela Round";
    font-style: normal;
    font-size: 20px;
    line-height: 130%;
    font-weight: 300;

    @include mq(750) {
        font-size: 18px;
    }

    @include mq(374) {
        font-size: 16px;
    }
}

// MPLUS fonts

.mplus-16-normal {
    font-family: "M PLUS Rounded 1c";
    font-style: normal;
    font-size: 16px;
    line-height: 110%;
}

.mplus-18-normal {
    font-family: "M PLUS Rounded 1c";
    font-style: normal;
    font-size: 18px;
    line-height: 110%;

    @include mq(750) {
        font-size: 16px;
    }
}

.mplus-20-normal {
    font-family: "M PLUS Rounded 1c";
    font-style: normal;
    font-size: 20px;
    line-height: 110%;
}

.mplus-32-normal {
    font-family: "M PLUS Rounded 1c";
    font-style: normal;
    font-size: 32px;
    line-height: 120%;

    @include mq(750) {
        font-size: 26px;
