.footer {
    padding: 100px 0 80px;

    &.dark-state {
        .f_link {
            a {
                color: $light-color;

                @include hover {
                    color: $primary-color;
                }
            }
        }

        .github {
            svg {
                fill: $light-color;
            }
        }
    }
}

.hz_line {
    width: 100vw;
    height: 2px;
    background: $light-color;

    &.dark-state {
        background: rgb(73, 72, 72);
    }
}

.f--inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px 20px 20px 50px;

    @include mq(1800) {
        width: 100%;
        padding: 20px 50px;
    }

    @include mq(1150) {
        flex-direction: column;
    }

    @include mq(650) {
        padding: 20px 20px;
    }

    @include mq(400) {
        padding: 20px 15px;
    }

    .c_content {
        margin-right: 0;
    }
}

.f_line {
    margin: 15px 0 10px;
}

.f_left {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 50%;

    @include mq(1700) {
        width: 55%;
    }

    @include mq(1480) {
        width: 58%;
    }

    @include mq(1150) {
        width: 100%;
        justify-content: flex-start;

        .c_content {
            margin-right: 100px;

            @include mq(700) {
                margin-right: 50px;
            }

            @include mq(585) {
                margin-right: 0;
            }
        }
    }

    @include mq(585) {
        flex-direction: column;

        .f_links {
            margin-top: 30px;
        }
    }
}

.f_right {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 32%;

    @include mq(1700) {
        width: 30%;
    }

    @include mq(1150) {
        width: 100%;
        justify-content: flex-start;
        margin-top: 50px;

        .f_links {
            margin-right: 150px;

            @include mq(700) {
                margin-right: 100px;
            }

            @include mq(400) {
                margin-right: 50px;
            }
        }
    }
}

.f_link {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 10px 0;
    svg{
        fill: $primary-color;
        path {
          stroke: none;
        }
    }

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }

    a{
        transition: 0.5s color;
        will-change: color;
    }

    @include hover {
        a {
           color: $primary-color; 
        }
    }
}

.f_social {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    .linkedin {
        margin-top: 0;
    }

    .github {
        svg {
            transition: 0.5s fill;
            will-change: fill;
            fill: $primary-color;
        }

        @include hover {
            svg {
               fill: $light-color; 
            }
        }
    }
}