/* Services-section */
.services-section{
    margin-top: 100px;
    padding-top: 120px;
}

.s_title {
    padding-top: 20px;
}

.s_line {
    margin: 15px 0 30px;
}

.s_link {
    margin-top: 15px;

    &-bold {
        color: $primary-color;
        font-weight: bold;
    }
}

.s_grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    grid-gap:30px;

    @include mq(480) {
        grid-template-columns: 1fr;
    }
}

.s_grid-box {
    position: relative;

    &:nth-child(2) {
        .grid_box-inner {
            background: $yellow-color;
        }
    }

    .grid_box-inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 97%;
        height: 96%;
        background: $orange-color;
        color: $black-color;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 30px;
        padding: 30px;

        @include mq(480) {
            padding: 15px;
        }
    }

    .grid_img-box {
        svg{
            width: 100px;
            height: 100px;
            margin-bottom: 15px;

            @include mq(480) {
                width: 75px;
                height: 75px;
            }
        }
    }

    .grid_title {
        text-align: center;
        padding-bottom: 10px;
    }
}