/* Menu (Lines) */
.sm_burger{
  position: relative;
  overflow: hidden;
  width: 50px;
  height: 50px;
  padding: 3px;
  margin: 15px auto 0 4px;
  cursor: none;
  display: none;
  transform: rotate(45deg) translateX(-20px);
  opacity: 0;

  @include mq(1350) {
    display: block;
  }

  &.open {
    .b_line{
      &:nth-child(1) {
        width: 40px;
        transform: translateY(0) rotate(45deg);
      }

      &:nth-child(2) {
        transform: translateX(60px);
      }

      &:nth-child(3) {
        width: 40px;
        transform: translateY(0) rotate(315deg);
      }
    }
  }
}

.b_line {
  position: absolute;
  top: 22px;
  left: 5px;
  width: 40px;
  height: 4px;
  border-radius: 4px;
  background: $primary-color;
  transition: 0.5s transform, 0.5s width;
  will-change: transform, width;

  &:nth-child(1) {
    width: 25px;
    transform: translateY(-15px);
    transition-delay: 0.125s;
  }

  &:nth-child(3) {
    width: 18px;
    transform: translateY(15px);
    transition-delay: 0.2s;
  }
}

/* Side-bar */
.side-menu{
  position: relative;
  margin-right: $side-menu-width;
  height: 100vh;

  @include mq(1350) {
    margin-right: $side-menu-width-phone;
  }

  &.dark-state {
    .sm_wrapper {
      border-right: solid 2px rgb(73, 72, 72);

      @include mq(1350) {
        background: $dark-color;
      }
    }

    .sm_item {
      color: $light-color;

      @include hover {
        color: $primary-color;
      }

      &.current-page{
        color: $primary-color;
        font-weight: bold;
      }
    }

    .sm_logo {
      svg {
        g {
          fill: #fff;
        }
      }
    }
  }
}

.sm_wrapper{
  position: fixed;
  height: 0;
  width: $side-menu-width;
  display: flex;
  flex-direction:column;
  justify-content: space-between;
  align-items: center;
  text-align: start;
  border-right: solid 2px $light-color;
  transition: 0.5s width;
  will-change: width;

  @include mq(1350) {
    width: $side-menu-width-phone;
    z-index: 30;
    background: $medium-color;

    &.show {
      width: $side-menu-width;

      .sm_list-item {
        visibility: visible;
        opacity: 1;
        transition: 0.5s visibility, 0.5s opacity;
        will-change: visibility, opacity;

        @for $i from 1 to 10 {
          &:nth-child(#{$i}) { 
            transition-delay: $i * 0.10s;
          }
        }
      }

      @include mq(370) {
        width: 100%;
      }
    }
  }
}

.sm_logo{
  margin-top: 45px;
  width: 100px;
  height: 71px;
  svg {
    width: 100px;
    height: 71px;
  }

  @include mq(1350) {
    display: none;
  }
}

.sm_item{
  padding: 10px 10px 10px 30px;
  transition: .5s color;
  will-change: color;

  &:hover {
    color: $primary-color;
  }
}

.sm_list-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 10px 0;

  &:first-child {
    margin: 0 0 10px 0;
  }

  &:last-child {
    margin: 10px 0 0 0;
  }

  @include mq(1350) {
    visibility: hidden;
    opacity: 0;
  }
}

.icon {
  width: 30px;
  height: 30px;
  svg{
    width: 100%;
    height: 100%;
    fill: $primary-color;
    path {
      stroke: none;
    }
  }
}