// Core style      
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
  
a{
    text-decoration: none;
    color: $dark-color;
    cursor: none;
}
  
li{
    list-style: none;
}
  
body{
    display: flex;
    flex-direction: row;
    background: $medium-color;
    cursor: none;

    &.dark-state {
        background: $dark-color;
        color: $light-color;
    }
}

.cursor {
    position: fixed;
    left: 0;
    top: 0;
    pointer-events: none;
}

.cursor--small {
    width: 16px;
    height: 16px;
    left: -8px;
    top: -8px;
    border-radius: 50%;
    z-index: 999;
    background: $dark-color-op;
    transition: 0.3s width, 0.3s height, 0.3s left, 0.3s top;
    will-change: width, height, left, top;

    &.hovered {
        width: 6px;
        height: 6px;
        left: -3px;
        top: -3px;
    }

    &.dark-state {
        background: $light-color;
    }

    @include mq(1080) {
        display: none;
    }
}

.cursor--big {
    width: 36px;
    height: 36px;
    left: -18px;
    top: -18px;
    border-radius: 50%;
    z-index: 999;
    background: transparent;
    border: 1px solid $dark-color-op;
    transition: 0.3s width, 0.3s height, 0.3s left, 0.3s top;
    will-change: width, height, left, top;

    &.hovered {
        width: 46px;
        height: 46px;
        left: -23px;
        top: -23px;
    }

    &.dark-state {
        border: 1px solid $light-color;
    }

    @include mq(1080) {
        display: none;
    }
}

.js-animate,
.js-animate2,
.js-animate3 {
    transform: scale(1.5);
    opacity: 0;
}

// Containers & etc..
.main-content {
    overflow: hidden;
}

.container{
    width: $max-width;
    overflow: hidden;
    padding: 0 20px 0 50px;

    @include mq(1800) {
        width: 100%;
        padding: 0 50px;
    }

    @include mq(650) {
        padding: 0 20px;
    }

    @include mq(400) {
        padding: 0 15px;
    }
}

.box{
    background: $medium-color;
    height: 270px;
    border-radius: 30px;
    box-shadow: $shadow;

    &.dark-state {
        box-shadow: $shadow-dark;
        background: $dark-color;
    }
}

.current-page{
    color: $primary-color;
    font-weight: bold;
}

/* Button's */
.btn {
  position: relative;
  z-index: 5;
  display: inline-block;
  padding: 0.7rem 1.5rem;
  border-radius: 50px;
  background-color: $medium-color;
  color: $primary-color;
  font-weight: bold;
  box-shadow:$shadow;
  transition:color .8s;
  overflow: hidden;
  cursor: none;

  .overlay {
    position: absolute;
    z-index: -1;
    
    transform:translate(-50%, -50%) scale(0);
    width: 300%;
    height: 500%;
    border-radius: 50%;
    background: $primary-color;
    color: $light-color;
    padding: 0.3rem;
    transition: .8s visibility, .8s transform ;
  
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    
    visibility: hidden;
  }

  @include hover {
    color:$light-color;

    .overlay {
        visibility: visible;
        transform:translate(-50%, -50%) scale(1);
    }
  }

  &.dark-state {
    box-shadow: $shadow-dark;
    color: $light-color;
    background: $dark-color;
  }
}

/* Light - dark button */
.sm_btn-wrapper {
    @include mq(1350) {
        margin-bottom: 15px;
        visibility: hidden;
        opacity: 0;

        &.show {
            will-change: visibility, opacity;
            transition: 0.5s opacity, 0.5s visibility;
            visibility: visible;
            opacity: 1;
        }
    }
}

.sm_btn {
    position: relative;
    width: 60px;
    height: 40px;
    -webkit-appearance: none;
    background: $medium-color;
    box-shadow: $shadow;
    border-radius: 30px;
    outline: none;
    cursor: none;
    margin-bottom: 30px;

    &::before {
        content: '';
        position: absolute;
        top: calc(50% - 12px);
        left: 10%;
        width: 23px;
        height: 23px;
        background: $dark-color;
        border-radius: 50%;
        z-index: 10;
        transition: .5s left, .5s background-color;
        will-change: left, background-color;
    }

    &::after {
        content: '';
        position: absolute;
        top: calc(50% - 3px);
        left: 25%;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background: $light-color;
        z-index: 15;
        transition: .5s left, .5s background-color;
        will-change: left, background-color;
    }

    &:checked {
        &::before {
            left: calc(100% - 30px);
            background: $light-color;
        }

        &::after {
            left: calc(73% - 5px);
            background: $primary-color;
        }
    }

    &.dark-state {
        background: $dark-color;
        box-shadow: $shadow-dark;
    }

    @include mq(1350) {
        margin-bottom: 0;
    }
}
  
/* Lines */
.line{
    background:$primary-color;
    width: 2rem;
    height: 4px;
    border-radius: 100px;
}