/* Home-section */
.home-section{
    position: relative;
    padding-top: 230px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
  
.hp_logo {
    position: absolute;
    top: 5%;
    right: 5%;
    display: none;

    svg{
        width: 100px;
        height: 71px;
    }

    &.dark-state {
        svg {
            g {
              fill: #fff;
            }
        }
    }

    @include mq(1350) {
        display: block;
    }
}


.hp_small-title {
    @include mq(385) {
        max-width: 200px;
    }
}

.hp_title{
    max-width: 750px;
    padding: 10px 0;
}
.hp_line {
    margin-bottom: 14px;
}

.hp_btn {
    margin-top: 50px;
}

.hp_text {
    @include mq(405) {
        br {
            display: none;
        }
    }
}

.hp_text-span{
    font-weight: bold;
}